#CalcFrame{
	display: grid;
	display: -ms-grid;    /* will need to position each button one at a time */
 	grid-template-columns: 1fr 1fr 1fr 1fr;
	-ms-grid-columns: 1fr 1fr 1fr 1fr; 
	-ms-grid-rows: 1fr 0.6fr 0.6fr 0.6fr 0.6fr 0.6fr;
	width: 200px;
	height: auto;
 	border-style: outset;
 	border-color:#808080;
  border-width: 5px;
 	padding: 5px;
  background-color: #808080;
  background-image: url("./MetalBg.jxr");
  margin: 5px;
  margin-left: auto;
  margin-right: auto;
}

#CalcDisplay{
	grid-column-start: 1;
	grid-column-end: 5;
	-ms-grid-column-span:4;
	font-size: 1.5em;
	width:auto;
    height: 25px;
	margin: 5px 15px 7px 15px;
	background-color: #1b263b;
	color: #E0E1DD ;
 }

.BasicCalcButton{
	background-color: #0d1d2a;
	color: #E0E1DD ;
  margin: 3px 4px 9px 4px;
  -ms-grid-row: 2;
  -ms-grid-column: 1;  	
}

