.NavList{
	display:flex;
	list-style: none;
	margin-left: 0px;
	margin-right: 0px;
	margin-top: 5px;
	margin-bottom: 5px;
	padding:0;
	background-color: #1b263b;
}

.NavItem, .NavItemLast {
	margin-left: 5px;
	margin-right: 5px;
	margin-top: 0px;
	margin-bottom: 0px;
	padding-top: 5px;
	padding-bottom: 5px;
	text-decoration: none;
	color : white;
	height: auto;
	background-color: #1b263b;
    opacity: 0.6;
    transition: 0.3s;
    border-width: 0px;
}

.NavItem:hover, .NavItemLast:hover{
	opacity: 1;
}

.NavItem:active, .NavItemLast:active{
    border-style: none;
}

.NavItem:focus, .NavItemLast:focus{
    outline: none;
}



.NavItemLast{
	margin-left: auto;
}


